.popup {
  $this: &;
  position: fixed;
  z-index: 25;
  overflow: auto;
  padding: 15px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba($black, .7);
  @include ready {
    @include tr(opacity .3s, visibility .3s); }
  @include hidden;
  &:not(#{$this}--full) {
    @include vertical; }
  &__inner {
    display: inline-block;
    vertical-align: middle;
    background-color: $white;
    font-size: $fz;
    width: 100%;
    max-width: 930px;
    position: relative;
    box-shadow: 0px 3px 77px 0px rgba(0, 0, 0, 0.33);
    padding: 100px 70px;
    @include xl {
      padding: 70px 50px; }
    @include lg {
      padding: 50px 30px; }
    @include md {
      padding: 40px 20px; }
    @include sm {
      padding: 40px 15px 15px;
      font-size: $fz-sm-px; } }
  &__close {
    color: $c-title; }
  &__video {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: none; }
  &__title {
    margin-bottom: 45px;
    @include lg {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 15px; } }

  &--active {
    @include visible;
    #{$this}__video {
      display: block; } }
  &--info {
    padding: 15px 30px 50px 15px;
    @include xl {
      padding: 15px 30px 50px 15px; }
    @include lg {
      padding: 15px 30px 50px 15px; }
    @include md {
      padding: 15px;
      padding-top: 40px; } }
  &--full {
    padding: 0;
    #{$this}__inner {
      max-width: none;
      display: block;
      height: 100%;
      width: 100%;
      padding: 0; }
    #{$this}__close {
      color: $white; } }
  &--form {
    #{$this}__inner {
      max-width: 690px;
      overflow: hidden; } }
  &--lg {
    #{$this}__inner {
      max-width: 1170px; } } }




.popup-content {
  text-align: left;
  @include flex;
  @include xs {
    display: block; }
  &__left {
    width: percentage(220/ 875);
    @include xs {
      width: auto;
      margin-bottom: 15px; } }
  &__content {
    width: percentage((875 - 220) / 875);
    padding-left: 30px;
    @include sm {
      padding-left: 15px; }
    @include xs {
      width: auto;
      padding-left: 0; } }
  &__img {
    font-size: 0;
    img {
      width: 100%;
      @include xs {
 } }        // width: auto
    // +cover
 }    // padding-top: 40%
  &__title {
    margin-bottom: 10px; }
  &__text {
    a {
      color: $blue;
      text-decoration: underline; }
    em {
      color: $accent;
      margin-bottom: 30px;
      display: block;
      @include sm {
        margin-bottom: 15px; } } } }
