.hero {
  $this: &;
  position: relative;
  color: $white;
  text-align: center;
  &__bg {
    @include cover;
    @include coverdiv;
    z-index: 0;
    background-color: $gray-light;
    @include before {
      @include coverdiv;
      z-index: 1;
      background-color: rgba($black, .7); }
    video {
      width: 100%;
      height: 100%;
      position: relative;
      z-index: 0;
      object-fit: cover; }

    &--no-cover {
      @include before {
        display: none; } } }
  &__inner {
    position: relative;
    z-index: 1;
    @include windowHeight($header-height);
    @include flex(center, normal, column);
    .has-shrinked-header & {
      @include windowHeight($header-height-shrinked); } }
  &__content {
    width: 100%;
    max-width: 750px;
    margin-left: auto;
    margin-right: auto; }
  &__upttl {
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; } }
  &__title {
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; } }
  &__subttl {
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } }
  &__btns {
    @include flex;
    margin-left: -6px;
    margin-right: -6px;
    @include sm {
      display: block;
      margin-left: 0;
      margin-right: 0; } }
  &__btn {
    margin-right: 6px;
    margin-left: 6px;
    width: 50%;
    @include sm {
      width: auto;
      margin-left: 0;
      margin-right: 0;
      @include notlast {
        margin-bottom: 10px; } } }
  &__meta {
    margin-bottom: 10px; }
  &__hashtags {
    color: $white; }

  &--sm {
    #{$this}__inner {
      height: auto;
      min-height: 295px; } }


  &--left {
    text-align: left;
    #{$this}__content {
      max-width: none; } } }
