.nav {
  @include font-title;
  letter-spacing: .1em;
  li {
    display: inline-block;
    margin-left: 50px;
    margin-right: 50px;
    @include xl {
      margin-right: 30px;
      margin-left: 30px; }
    @include lg {
      display: block;
      margin-bottom: 1em;
      margin-left: 0;
      margin-right: 0; } }
  a {
    @include tr(color .3s);
    @include hover {
      color: $accent; } }
  &__link {
    &--current {
      color: $accent; } }

  &--footer {
    @include font-content;
    letter-spacing: inherit;
    li {
      display: block;
      margin-left: 0;
      margin-right: 0;
      @include notlast {
        margin-bottom: em(10); }
      @include xl {
        margin-left: 0;
        margin-right: 0; } } } }
