.section {
  $this: &;
  position: relative;
  padding-top: 100px;
  padding-bottom: 100px;
  @include xl {
    padding-top: 80px;
    padding-bottom: 80px; }
  @include lg {
    padding-top: 60px;
    padding-bottom: 60px; }
  @include md {
    padding-top: 40px;
    padding-bottom: 40px; }
  @include sm {
    padding-top: 30px;
    padding-bottom: 30px; }
  &__title {
    margin-bottom: 40px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; }
    &--mb-sm {
      margin-bottom: 15px; } }
  &__side-title {
    background-color: $gray-lightest;
    padding-bottom: 20px;
    @include md {
      padding-top: 20px; } }
  &__img {
    @include cover;
    width: 100%;
    display: block;
    text-align: left;
    height: 100%;
    padding-top: 50%;
    position: relative;
    background-color: $gray-light;
    @include sm;

    iframe {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0; }
    .play-img {
      @include vcenter; } }


  &__img-wrap {
    font-size: 0;
    margin-bottom: 30px;
    img {
      width: 100%; }
    @include md {
      margin-bottom: 15px; } }

  &__side-content, &__side-title {
    padding-left: percentage(2/12);
    @include md {
      padding-left: 70px; } }
  &__side {
    position: relative;
    @include before {
      position: absolute;
      background-color: $gray-lightest;
      top: 0;
      right: 0;
      left: 0;
      z-index: 0;
      top: -100px;
      bottom: 100%;
      @include xl {
        top: -80px; }
      @include lg {
        top: -60px; }
      @include md {
        display: none; } } }
  &__btns {
    // @extend .hero__btns
    display: flex;
    justify-content: center;
    @include lg {
      display: block;
      margin-left: 0;
      margin-right: 0; }
    #{$this}__btn {
      // padding-top: 0
      @include notlast {
        margin-right: 10px;
        @include lg {
          margin-right: 0; } }
      @include notfirst {
        @include lg {
          margin-top: 10px; } } } }
  &__btn {
    text-align: center;
    margin-top: 40px; }
  &__block {
    @include notlast {
      margin-bottom: 80px;
      @include xl {
        margin-bottom: 60px; }
      @include lg {
        margin-bottom: 40px; }
      @include md {
        margin-bottom: 20px; } }
    &--md {
      @include notlast {
        margin-bottom: 50px;
        @include xl {
          margin-bottom: 50px; } } } }
  &__form {
    max-width: 530px; }
  &__select {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
    @include lg {
      display: block;
      .title {
        margin-right: 0;
        margin-bottom: 15px; } }
    @include md {
      margin-bottom: 15px; }
    .title {
      margin-right: 10px; } }



  &--color {
    background-color: $accent;
    @include image('bg-color.jpg');
    @include cover;
    color: $white; }
  &--border {
    border-top: 1px solid $gray-lightest;
    border-bottom: 1px solid $gray-lightest; }
  &--border-bottom {
    border-bottom: 1px solid #e1e1e1; }
  &--gray {
    background-color: #eeeeee; }
  &--light {
    background-color: #fafafa; } }

.section-btns {
  @extend .hero__btns;
  max-width: 750px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  @include sm {
 }    // display: block
  @include md {
    margin-top: 15px; }
  &__btn {
    @extend .hero__btn; } }

.s-info {
  padding-top: 200px;
  padding-bottom: 0 !important;
  @include xl {
    padding-top: 160px; }
  @include lg {
    padding-top: 120px; }
  @include md {
    padding-top: 80px; }
  @include sm {
    padding-top: 60px; } }

.s-subscribe {
  &__form {
    max-width: 540px; } }

.s-dude {
  margin-top: 200px;
  > .container {
    position: relative;
    z-index: 1; }
  @include xl {
    margin-top: 100px; }
  @include lg {
    margin-top: 50px; }
  @include md {
    margin-top: 0; }
  &__img {
    position: absolute;
    z-index: 0;
    bottom: 0;
    right: 10%;
    max-width: 35%;
    @include xs {
      display: none; } } }

