.list {
  font-weight: 700;
  li {
    @include notlast {
      margin-bottom: 10px; }
    &::before {
      content: '-';
      display: inline-block;
      margin-right: .2em; } } }

.blockquote, blockquote {
  background-color: #f7f7f7;
  padding: 20px;
  border-left: 5px solid  $accent; }
