.card {
  $this: &;

  display: block;
  background-color: $white;
  box-shadow: 0px 0px 10px 0px rgba(1, 0, 0, 0.09);
  @include tr(box-shadow .3s);
  @include hover {
    box-shadow: 0px 0px 21px 0px rgba(1, 0, 0, 0.18); }

  &__img {
    display: block;
    @include cover;
    background-color: $gray-light;
    padding-top: 60%; }

  &__meta {
    background-color: #fafafa;
    color: $accent;
    padding: 10px 20px; }

  &__content {
    display: block;
    padding: 35px 20px;
    @include lg {
      padding: 20px; }
    @include md {
      padding: 20px 15px; } }

  &__title {
    display: block;
    margin-bottom: 12px;
    a {
      @include hover {
        text-decoration: underline; } }
    &.title-under {
      a {
        @include tr(color .3s);
        @include hover {
          text-decoration: none;
          color: $accent; } } }
    &--lg {
      color: $accent;
      text-transform: uppercase;
      font-size: em(30); } }


  &__text {
    margin-bottom: 15px; }

  &__people {
    margin-bottom: 20px; } }

.card-people {
  display: flex;
  &__title {
    // width: 20%
    margin-right: 10px; }
  &__list {
 } }    // width: 80%

.card-meta {
  display: flex;

  &__item {
    display: flex;
    align-items: center;

    @include font-title;
    @include notlast {
      margin-right: 20px; }
    .icon {
      margin-right: .5em; } } }
