.block {
  box-shadow: 0px 0px 38px 0px rgba(0, 0, 0, 0.09);
  background-color: $white;
  padding: 40px;
  @include xl {
    padding: 30px; }
  @include lg {
    padding: 20px; }
  @include md {
    padding: 15px; }
  &__el {
    @include notlast {
      margin-bottom: 15px; } }
  &__text {
    max-width: 550px;
    p {
      @include notlast {
        margin-bottom: 15px; } } }
  &__btn {
    text-align: center;
    position: relative;
    z-index: 1; }
  &__top {
    display: flex;
    align-items: center;
    // justify-content: space-between
    @include xl {
      display: block; } }
  &__top-logo {
    display: inline-block;
    margin-right: 50px;
    @include xl {
      display: block;
      margin-bottom: 10px; } }

  &--gray {
    background-color: $gray-lightest; }

  &--equal {
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column; }

  &--transp {
    background-color: transparent;
    box-shadow: none;
    padding: 0 !important; } }
