.logos {
  display: flex;
  flex-wrap: wrap;

  position: relative;
  @include before {
    position: absolute;
    z-index: 1;
    top: 0;
    bottom: 0;
    right: 0;
    width: 2px;
    background-color: $white; }
  @include after {
    position: absolute;
    z-index: 1;
    height: 2px;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: $white; }

  &__item {
    width: 25%;
    padding: 30px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;

    border-bottom: 1px solid $gray-lightest;
    border-right: 1px solid $gray-lightest;

    @include sm {
      width: 33.33333%; }
    @include xs {
      width: 50%; }

    img {
      max-height: 100%; } } }
