.article {
  @include clr;

  h1, h2, h3, h4, h5, h6 {
    @extend .title; }

  h2 {
    @extend .title--h2;
    @include notlast {
      margin-bottom: 50px;
      @include md {
        margin-bottom: 30px; } } }

  h3 {
    @extend .title--h3;
    @include notlast {
      margin-bottom: 20px; } }

  p, ul, ol, blockquote, img {
    @include notlast {
      margin-bottom: 25px; } }

  blockquote {
    max-width: 693px;
    margin-left: auto;
    margin-right: auto; }
  ul {
    @extend .list;
    &:not(.list) {
      font-weight: 400;
      li {
        @include notlast {
          margin-bottom: 0; } } } }

  a {
    color: $blue;
    text-decoration: underline; }

  img {
    &.alignright {
      float: right;
      margin-left: 30px;
      @include md {
        float: none;
        width: 100%;
        height: auto;
        margin-left: 0; } }
    &.alignleft {
      float: left;
      margin-right: 30px;
      @include md {
        float: none;
        width: 100%;
        margin-right: 0; } } } }
